import React, { useState, useCallback } from 'react';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import IconArrowDown from '@material-ui/icons/KeyboardArrowDown';
import IconArrowRight from '@material-ui/icons/KeyboardArrowRight';
import IconCheckBox from '@material-ui/icons/CheckBoxOutlineBlank';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import styles from './Sessions.module.css';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import { FakeDataSessions } from './FakeData';
import { Session } from './Interfaces';

const FakeData = FakeDataSessions.map((item: any) => {
    item.name = `${item.lname}, ${item.fname}`;
    item.nameLower = item.name.toLowerCase();
    return item;
});

interface Props {
    user: any;
    onSelectSession: (session: Session) => void;
}
enum Mode {
    Name = 'Name',
    Rank = 'Rank',
    Platform = 'Platform',
    Date = 'Date',
}
enum Rank {
    All = 'All',
    Captain = 'Captain',
    Pilot = 'Pilot',
    Other = 'Other'
}
enum Platform {
    All = 'All',
    Landing4 = 'Landing 4',
    Landing3 = 'Landing 3',
    Takeoff = 'Takeoff'
}

const getSortKey = (mode: string) =>
{
    return (mode === Mode.Name && 'nameLower') ||
           (mode === Mode.Rank && 'rank') ||
           (mode === Mode.Platform && 'platform') ||
           (mode === Mode.Date && 'date') || '';
};

const sort = (data: any[], mode: Mode, search: string, rank: string, platform: string, date: string) =>
{
    let groups: { [ key: string ]: Session[] } = {},
        sortKey = getSortKey(mode);

    data.forEach((item: any) =>
    {
        if ((rank === 'All' || item.rank === rank) &&
            (platform === 'All' || item.platform == platform) &&
            (date === 'All' || item.date === date) &&
            (!search || item[sortKey].includes(search)))
        {
            let sortVal = item[sortKey];
            if (!groups[sortVal])
            {
                groups[sortVal] = [];
            }
            groups[sortVal].push(item);
        }
    });

    let keysSorted = Object.keys(groups).sort(),
        out = keysSorted.map(id => groups[id]);

    return out;
};

const addTab = (name: string, mode: string) =>
{
    return <Tab key={name} label={name} value={name} className={styles.tab} />
};

const addMenuItem = (name: string) =>
{
    return <MenuItem key={name} value={name} className={styles.menuItem}>{name}</MenuItem>
};

const addSelect = (name: string, currentValue: string, setValue: (value: any) => void, values: string[]) =>
{
    const onChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
        setValue(e.target.value);
    };

    return (
        <div className={styles.selectWrapper}>
            <InputLabel className={styles.inputLabel} htmlFor={name}>{name}</InputLabel>
            <Select
                className={styles.select}
                value={currentValue}
                onChange={onChange}
                inputProps={{
                    name: name,
                    id: name,
                }}
            >
                {values.map(addMenuItem)}
            </Select>
        </div>
    );
};

const renderHeader = (first: Session, mode: Mode, isExpanded: boolean, expand: () => void) =>
{
    let content;
    if (mode === Mode.Name)
    {
        content = (
            <>
                <div className={styles.name} onClick={expand}>{first.name}</div>
                <div className={styles.rank}>{first.rank}</div>
            </>
        );
    }
    else if (mode === Mode.Rank)
    {
        content = (
            <>
                <div className={styles.name} onClick={expand}>{first.rank}</div>
            </>
        );
    }
    else if (mode === Mode.Platform)
    {
        content = (
            <>
                <div className={styles.name} onClick={expand}>{first.platform}</div>
            </>
        );
    }
    else if (mode === Mode.Date)
    {
        content = (
            <>
                <div className={styles.name} onClick={expand}>{first.date}</div>
            </>
        );
    }

    return (
        <div key={'h' + first.id} className={`${styles.row} ${styles.rowGroup}`}>
            <IconButton className={styles.menuButton} color="inherit" aria-label="Menu" onClick={expand}>
                {isExpanded ? <IconArrowDown /> : <IconArrowRight />}
            </IconButton>
            {content}
        </div>
    );
}

const Sessions: React.FunctionComponent<Props> = (p: Props) =>
{
    const [ search, setSearch ] = useState('');
    const [ mode, setMode ] = useState(Mode.Name);
    const [ rank, setRank ] = useState(Rank.All);
    const [ platform, setPlatform ] = useState(Platform.All);
    const [ date, setDate ] = useState('All');
    const [ expanded, setExpanded ] = useState<any>({});

    const onChangeTab = useCallback((e: any, value: any) => {
        setMode(value);
    }, []);

    const onChangeSearch = useCallback((e: any) => {
        setSearch(e.target.value);
    }, []);

    const onClickExpand = useCallback((key: any) => {
        let newVal = Object.assign({}, expanded);
        newVal[key] = !newVal[key];

        setExpanded(newVal);
    }, [ expanded ]);

    let groups = sort(FakeData, mode, search, rank, platform, date);

    return (
        <div className={styles.sessions}>
            <Tabs className={styles.tabs} value={mode} onChange={onChangeTab}>
                {addTab(Mode.Name, mode)}
                {addTab(Mode.Rank, mode)}
                {addTab(Mode.Platform, mode)}
                {addTab(Mode.Date, mode)}
            </Tabs>
            <Paper>
                <div>
                    <div className={`${styles.row} ${styles.headerRow}`}>
                        <TextField
                            label={mode}
                            variant="outlined"
                            margin="dense"
                            onChange={onChangeSearch}
                            InputProps={{ classes: { input: 'textField' } }}
                        />
                        {addSelect('Rank', rank, setRank, [Rank.All, Rank.Captain, Rank.Pilot, Rank.Other])}
                        {addSelect('Platform', platform, setPlatform, [Platform.All, Platform.Landing3, Platform.Landing4, Platform.Takeoff])}
                        {addSelect('Date', date, setDate, ['All'])}
                    </div>
                    <div>
                        {groups.map((group: Session[]) => {
                            let first = group[0],
                                key = (first as any)[getSortKey(mode)],
                                isExpanded = expanded[key],
                                expand = () => onClickExpand(key);

                            return (
                                <>
                                    {renderHeader(first, mode, isExpanded, expand)}
                                    {isExpanded && group.map(row => (
                                        <div key={row.id} className={`${styles.row} ${styles.rowItem}`}>
                                            <IconButton className={styles.menuButton} color="inherit" aria-label="Menu">
                                                <IconCheckBox />
                                            </IconButton>
                                            <div className={styles.nowrap} onClick={() => p.onSelectSession(row)}>
                                                <div className={styles.platform}>{row.platform}</div>
                                                <div className={styles.date}>{row.date}</div>
                                            </div>
                                        </div>
                                    ))}
                                </>
                            );
                        })}
                    </div>
                </div>
            </Paper>
        </div>
    );
}

export default Sessions;