import React, { useState, useCallback } from 'react';
import Sessions from './Sessions';
import SessionDetails from './SessionDetails';
import Button from '@material-ui/core/Button';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import IconBack from '@material-ui/icons/ArrowBack';
import IconPerson from '@material-ui/icons/Person';
import { Session } from './Interfaces';
import styles from './Main.module.css';

interface Props {
    user: any;
}
enum Mode {
    Sessions = 'Sessions',
    SessionDetails = 'Session Details',
}
const Subjects: React.FunctionComponent<Props> = (p: Props) =>
{
    const [ selectedSession, setSelectedSession ] = useState<Session | undefined>(undefined);

    const goBack = useCallback(() => setSelectedSession(undefined), []);

    return (
        <div className={styles.container}>
            <AppBar position="static" className={styles.appBar}>
                <Toolbar>
                    {selectedSession && (
                        <IconButton className={styles.menuButton} color="inherit" aria-label="Menu">
                            <IconBack onClick={goBack} />
                        </IconButton>
                    )}
                    <Typography variant="h6" color="inherit">
                        {selectedSession ? 'Session Details' : 'Sessions'}
                    </Typography>
                    <div className={styles.grow} />
                    <Button className={styles.menuButton} color="inherit" aria-label="Menu">
                        <IconPerson className={styles.person} />
                        User
                    </Button>
                </Toolbar>
            </AppBar>
            <div className={styles.main}>
                {selectedSession && <SessionDetails user={p.user} session={selectedSession} />}
                {!selectedSession && <Sessions user={p.user} onSelectSession={setSelectedSession} />}
            </div>
        </div>
    );
}

export default Subjects;